export function drawerAnimation(placement) {
  let originX;
  let originY;
  switch (placement) {
    case 'top':
      originY = '-100%';
      break;
    case 'bottom':
      originY = '100%';
      break;
    case 'left':
      originX = '-100%';
      break;
    case 'right':
      originX = '100%';
      break;
    default:
      originX = '100%';
  }
  return {
    initial: {
      x: `${originX}`,
      y: `${originY}`
    },
    animate: {
      x: 0,
      y: 0
    },
    exit: {
      x: `${originX}`,
      y: `${originY}`
    }
  };
}
