import { cs } from '@mx-design/web-utils';
import { useMemo } from 'react';
export function useClassNames(props) {
  const {
    getPrefixCls,
    position
  } = props;
  const prefixCls = getPrefixCls('message');
  return useMemo(() => ({
    wrapperClassNames: cs(`${prefixCls}-wrapper`, `${prefixCls}-wrapper-${position}`)
  }), [prefixCls, position]);
}