import { useMemo } from 'react';
import { cs } from '@mx-design/web-utils';
export function useClassNames(props) {
  const {
    spin,
    getPrefixCls,
    className
  } = props;
  const prefixCls = getPrefixCls('icon');
  return useMemo(() => ({
    iconCls: cs({
      [`${prefixCls}-spin`]: spin
    }, className, prefixCls)
  }), [className, prefixCls, spin]);
}