import { useEffect, useRef } from 'react';
import { resetContainerStyle, setContainerStyle } from '../utils';
export function useOverflowHidden(getContainer, hidden) {
  const needResetContainerStyle = useRef(false);
  const originContainerStyle = useRef({});
  useEffect(() => {
    hidden ? setContainerStyle({
      needResetContainerStyle,
      originContainerStyle,
      getContainer
    }) : resetContainerStyle({
      needResetContainerStyle,
      originContainerStyle,
      getContainer
    });
    return () => {
      resetContainerStyle({
        needResetContainerStyle,
        originContainerStyle,
        getContainer
      });
    };
  }, [getContainer, hidden]);
}