import { cs } from '@mx-design/web-utils';
import { useMemo } from 'react';
export function useModalClassNames(props) {
  const {
    getPrefixCls,
    className,
    mask
  } = props;
  const prefixCls = getPrefixCls('modal');
  return useMemo(() => ({
    maskCls: cs(`${prefixCls}-mask`),
    wrapperCls: cs(`${prefixCls}-container`, {
      [`${prefixCls}-container-no-mask`]: !mask
    }, className)
  }), [className, prefixCls, mask]);
}